<!-- 价格1 -->
<template>
	<view class="priceone">
		<view class="price">
			<view class="newprice">￥<span>{{ pricedata.price }}</span></view>
			<view class="oldprice">原价￥<span>{{ pricedata.oldprice }}</span></view>
		</view>
		<view class="name">
			{{ pricedata.name }}
			<span v-for="(item,index) in pricedata.label" :key="index" :style="{
					color: extract(item,'color'),
					background:'url('+extract(item,'image')+') no-repeat',
					border:extract(item,'bdstat')+'px solid'+ extract(item,'color'),
					'border-radius':extract(item,'bjstat')+'px',
					padding:extract(item,'bdstat')?'1px':'2px'
				}"
			>{{ extract(item,"text") }}</span>
		</view>
		<view class="butlist">
			<view class="bux" @click="shouchang"><image class="img" src="../../../static/img/shopsc.png"></image><p>收藏</p></view>
			<view class="bux"><image class="img" src="../../../static/img/shopxl.png"></image><p>销{{ sum() }}</p></view>
			<view class="bux" @click="fenxiang"><image class="img" src="../../../static/img/shopfx.png"></image><p>分享</p></view>
		</view>
	</view>
</template>

<script>
	export default {
		name:"priceone",
		data() {
			return {
			}
		},
		props:{
			pricedata:{
				type:Object,
				default:() => {
					name:""
					price:0
					oldprice:0
					salesum:0
					lasbel:[]
				},
			}
		},
		onShow() {
		},
		methods: {
			extract(a,b){
				if(b == "image"){
					return uni.http + uni.label[a][b];
				}
				return uni.label[a][b];
			},
			//归纳数量
			sum(){
				var sumdata = String(this.pricedata.salesum).split("");
				if(sumdata.length>4){
					return sumdata[0]+"万+"
				}
				if(sumdata.length>3){
					return sumdata[0]+"千+"
				}
				return this.pricedata.salesum;
			},
			//收藏商品
			shouchang(){
				this.$emit('shouchang');
			},
			//分享商品
			fenxiang(){
				this.$emit('fenxiang');
			}
		},
		
	}
</script>
<style lang="scss" scoped>
.priceone{
	width: 86%;
	padding: 10px 4%;
	margin:15px 3% 0px;
	background: #fff;
	border-radius: 10px;
}
.price{
	font-size: $uni-text-font;
	display:flex;
	align-items:flex-end;
}
.newprice{
	color: $uni-price-color;
}
.newprice span{
	font-size: $uni-theme-font;
}
.oldprice{
	line-height:25px;
	font-family: "Microsoft YaHei";
	margin-left: 10px;
	color: #999;
}
.oldprice span{
	text-decoration:line-through;
}
.name{
	height: 40px;
	margin: 5px 0px;
	font-size: $uni-title-font;
	line-height: 20px;
	overflow: hidden;
}
.name span{
	margin-left: 5px;
	font-size: $uni-span-font;
}
.butlist{
	display: flex;
	justify-content: space-between;
}
.bux{
	padding: 5px 10px;
	font-family:"Microsoft YaHei";
	color: #999;
	font-size: $uni-text-font;
	height: 20px;
	display: flex;
	align-items: center;
}
.bux .img{
	width: 16px;
	height: 16px;
	vertical-align:middle;
}
</style>
